<template>
	<div class="search_container">
		<el-form :inline="true" :model="params" class="demo-form-inline">
			<el-form-item label="业务日期" size="mini">
				<el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
				</el-date-picker>
			</el-form-item>
			<el-form-item label="单据号" size="mini">
				<el-input v-model="params.prepaid_sn" maxlength="100"></el-input>
			</el-form-item>
			<el-form-item label="财务组织" size="mini">
				<el-select clearable v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="companyChange" filterable maxlength="100">
					<el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
						<span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="部门" size="mini">
				<el-select clearable v-model="params.department_id" placeholder="请选择" @focus="DepartmentListInvok" @change="departmentChange" filterable maxlength="100">
					<el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="供应商(厂家)" size="mini">
				<el-select clearable v-model="params.supplier_id" maxlength="100" @change="supplierChangesave" :filter-method="supplierListInvok" @focus="supplierListInvok" filterable placeholder="请选择">
					<el-option v-for="item in supplierList" :label="item.supplier_name" :value="item.supplier_id" :key="item.supplier_id">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="审核状态" size="mini">
				<el-select clearable v-model="params.auditor_status">
					<el-option label="全部" :value="0">全部</el-option>
					<el-option label="未审核" :value="1">未审核</el-option>
					<el-option label="已审核" :value="2">已审核</el-option>
					<el-option label="作废" :value="3">作废</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="审核日期" size="mini">
				<el-date-picker v-model="timeValue1" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" clearable>
				</el-date-picker>
			</el-form-item>
			<el-form-item size="mini" class="search-style search">
				<el-button @click="onSearch" size="mini" type="primary">查询</el-button>
				<el-button plain @click="onReset" size="mini">重置</el-button>
			</el-form-item>
		</el-form>
		<div class="add_delet">
			<el-button icon="el-icon-circle-plus-outline" type="primary" class="addBtn" size="mini" @click="addFun" v-if="authority.b_add==1">新增</el-button>
			<el-button plain icon="el-icon-delete" size="mini" @click="delFun" v-if="authority.b_del==1">删除</el-button>
			<el-button class="iconfont icon-pass" type="primary" size="mini" @click="examineFun" v-if="authority.b_pass==1">审核</el-button>
			<el-button plain @click="AUDIT_GET" v-if="authority.b_nopass==1" size="mini">弃审</el-button>
			<el-button plain class="iconfont icon-zuofei" size="mini" @click="abolishFun" v-if="authority.b_invalid==1">作废</el-button>
		</div>
		<div class="table_container">
			<el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" @selection-change="handleSelectionChange" max-height="600" style="width: 100%" :row-style="selectRowChangeBg" @row-dblclick='handleClick(TableData.tableData,$event)'>
				<el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44" @selection-change="handleSelectionChange">
				</el-table-column>
				<el-table-column :show-overflow-tooltip="true" property="registe_time" width="100" label="操作" fixed="right">
					<template slot-scope="scope">
						<el-button @click="handleClick(scope,scope.row)" type="text" size="small" v-if="authority.b_info==1">查看</el-button>
						<el-button @click="updateClick(scope.row)" type="text" size="small" v-if="scope.row.auditor_status==1 && authority.b_edit==1">修改</el-button>
						<el-button disabled type="text" size="small" v-else>
							修改
						</el-button>
					</template>
				</el-table-column>
				<el-table-column :show-overflow-tooltip="true" property="company_name" width="150" label="财务组织"></el-table-column>
				<!-- <el-table-column :show-overflow-tooltip="true" property="prepaid_sn" width="180" label="单据号"></el-table-column> -->
				<el-table-column :show-overflow-tooltip="true" property="prepaid_sn" width="180" label="单据号">
					<template slot-scope="scope">
						<el-button @click="handleClick(scope,scope.row)" type="text" size="small">
							{{scope.row.prepaid_sn}}
						</el-button>
					</template>
				</el-table-column>
				<el-table-column :show-overflow-tooltip="true" property="supplier_name" width="150" label="供应商(厂家)"></el-table-column>
				<el-table-column :show-overflow-tooltip="true" property="total_price" width="180" label="冲销金额"></el-table-column>
				<el-table-column :show-overflow-tooltip="true" property="department_name" width="100" label="部门">
					<template slot-scope="scope">
						<pre>{{scope.row.department_name  | priceFormatNone}}</pre>
					</template>
				</el-table-column>
				<el-table-column :show-overflow-tooltip="true" property="add_user_name" width="180" label="创建人"></el-table-column>
				<el-table-column :show-overflow-tooltip="true" property="business_time" width="180" label="业务日期"></el-table-column>
				<el-table-column :show-overflow-tooltip="true" property="add_time" width="180" label="单据时间"></el-table-column>
				<el-table-column :show-overflow-tooltip="true" property="auditor_status_name" width="180" label="审核状态"></el-table-column>
				<el-table-column :show-overflow-tooltip="true" property="auditor_time" width="180" label="审核时间"></el-table-column>
				<el-table-column :show-overflow-tooltip="true" property="update_user_name" width="180" label="修改人"></el-table-column>
				<el-table-column :show-overflow-tooltip="true" property="update_time" width="180" label="修改时间"></el-table-column>
				<el-table-column :show-overflow-tooltip="true" property="remark" width="180" label="备注"></el-table-column>
			</el-table>
		</div>
		<div class="page_container">
			<div class="pageNumber">每页显示</div>
			<el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	import { getData, opthionList } from "common/utils/public_function/commonUtils";
	import { mapActions, mapGetters } from "vuex";
	export default {
		name: "purchasePrepaidhandleDefault",
		data() {
			return {
				authority: {},
				formInlineParams: {},
				timeValue: [],
				timeValue1: [],
				TableData: {
					tableData: []
				},
				multipleSelection: []
			};
		},
		computed: {
			...mapGetters(["prepaidhandleDefaultGet"])
		},
		methods: {
			...mapActions([
				"prepaidhandleDefaultIndexACT",
				"prepaidhandleAdd_show",
				"prepaidhandleSee_show"
			]),
			supplierChangesave(val) {
				if (val) {
					this.params.supplier_name = this.supplierList.filter(
						obj => obj.supplier_id === val
					)[0].supplier_name;
				}
			},
			departmentChange(val) {
				if (val) {
					this.params.department_name = this.departmentList.filter(
						obj => obj.department_id === val
					)[0].department_name;
				}
			},
			selectRowChangeBg({ row, rowIndex }) {
				if (
					this.multipleSelection.length &&
					this.multipleSelection
						.map(obj => obj.prepaid_id)
						.includes(row.prepaid_id)
				) {
					return "background:#d4e1ee";
				} else {
					return "";
				}
			},
			companyChange(val) {
				if (val != "") {
					this.params.company_name = this.companyList.filter(
						obj => obj.company_id === val
					)[0].company_name;
				}
				this.$set(this.params, "supplier_id", "");
				this.$set(this.params, "department_id", "");
			},
			onSearch() {
				if (this.timeValue) {
					this.params.start_time = this.timeValue[0];
					this.params.end_time = this.timeValue[1];
				} else {
					this.params.start_time = "";
					this.params.end_time = "";
				}
				if (this.timeValue1) {
					this.params.start_auditor_time = this.timeValue1[0];
					this.params.end_auditor_time = this.timeValue1[1];
				} else {
					this.params.start_auditor_time = this.params.end_auditor_time = "";
				}
				this.prepaidhandleDefaultIndexACT({
					params: this.params,
					goPage: this.goPage,
					pageSize: this.pageSize
				});
				Object.assign(this.formInlineParams.params, this.params, {
					page: 1
				});
				getData(this.formInlineParams);
			},
			onReset() {
				this.$set(this, "params", {});
				this.$set(this, "formInlineParams", {
					params: this.params, //1默认带的参数
					TableData: this.TableData, //列表数据
					API: "getPrepaidList", //接口名
					vm: this //传递的this
				});
				this.timeValue = [];
				this.timeValue1 = [];
				this.onSearch();
			},
			addFun() {
				this.$router.push({ path: "/purchase/prepaidhandle/prepaidhandleAdd" });
				//新增
				this.prepaidhandleAdd_show({
					// commont: "prepaidhandleAdd",
					addORchange: "add"
				});
			},
			handleClick(scope, row) {
				//查看
				this.$router.push({
					path: "/purchase/prepaidhandle/prepaidhandleSearch"
				});
				if (row.prepaid_id != null) {
					this.prepaidhandleSee_show({
						// commont: "prepaidhandleSee",
						prepaid_id: row.prepaid_id,
						changePage: "prepaidhandle",
						params: this.params,
						goPage: this.goPage,
						pageSize: this.pageSize
					});
				}
			},
			// 弃审
			AUDIT_GET() {
				if (this.multipleSelection.length < 1) {
					this.$message({
						message: "请选择需要弃审的信息",
						type: "warning"
					});
					return false;
				}
				var addr = [];

				for (var i = 0; i < this.multipleSelection.length; i++) {
					addr.push(this.multipleSelection[i].prepaid_id);
				}
				var orderArr = addr.toString();
				this.$axMethod
					.unAuditorPrepaid({
						prepaid_id: orderArr
					})
					.then(res => {
						if (res.data.code == 200) {
							this.$message({
								message: res.data.msg,
								type: "success"
							});
							getData(this.formInlineParams); //获取采购订单列表
						}
					});
				//弃审
			},
			updateClick(row) {
				//修改
				this.$router.push({ path: "/purchase/prepaidhandle/prepaidhandleAdd" });
				this.prepaidhandleAdd_show({
					// commont: "prepaidhandleAdd",
					prepaid_id: row.prepaid_id,
					addORchange: "change",
					params: this.params,
					goPage: this.goPage,
					pageSize: this.pageSize
				});
			},
			delFun() {
				//删除
				var deledata = {
					deleID: "prepaid_id",
					deleSelect: this.multipleSelection,
					deleApi: "delPrepaid",
					list: {
						UrlApi: "getPrepaidList",
						data: this.TableData,
						esData: {
							page: this.goPage
						}
					}
				};
				this.$common.deletes(deledata, "删除", this);
			},
			examineFun() {
				//审核
				if (this.multipleSelection.length < 1) {
					this.$message({
						message: "请选择需要审核的信息",
						type: "warning"
					});
					return false;
				}
				var addr = [];
				for (var i = 0; i < this.multipleSelection.length; i++) {
					addr.push(this.multipleSelection[i].prepaid_id);
				}
				var rebateArr = addr.toString();
				var funcan = {
					examineID: "prepaid_id",
					eachData: {
						prepaid_id: rebateArr
					},
					examineApi: "auditorPrepaPass",
					list: {
						UrlApi: "getPrepaidList",
						data: this.TableData,
						esData: {
							page: this.goPage
						}
					}
				};
				this.$common.examineFun(funcan);
			},
			abolishFun() {
				//作废
				if (this.multipleSelection.length < 1) {
					this.$message({
						message: "请选择需要作废的信息",
						type: "warning"
					});
					return false;
				}
				var addr = [];
				for (var i = 0; i < this.multipleSelection.length; i++) {
					addr.push(this.multipleSelection[i].prepaid_id);
				}
				var rebateArr = addr.toString();
				var funcan = {
					examineID: "prepaid_id",
					eachData: {
						prepaid_id: rebateArr
					},
					examineApi: "auditorPrepaidInvalid",
					list: {
						UrlApi: "getPrepaidList",
						data: this.TableData,
						esData: {
							page: this.goPage
						}
					}
				};
				this.$common.examineFun(funcan);
			},
			handleSelectionChange(val) {
				this.multipleSelection = val;
			},
			handleSizeChange(val) {
				Object.assign(this.formInlineParams.params, {
					per_page: val,
					page: 1
				});
				getData(this.formInlineParams);
			},
			handleCurrentChange(val) {
				Object.assign(this.formInlineParams.params, {
					page: val
				});
				getData(this.formInlineParams);
			}
		},
		mounted() {
			this.authority = JSON.parse(localStorage.getItem("authority"));

			if (this.prepaidhandleDefaultGet && this.prepaidhandleDefaultGet.params) {
				if (
					this.prepaidhandleDefaultGet.params.start_time ||
					this.prepaidhandleDefaultGet.params.start_auditor_time
				) {
					const {
						start_time,
						end_time,
						start_auditor_time,
						end_auditor_time
					} = this.prepaidhandleDefaultGet.params;
					this.timeValue = this.timeValue.concat([start_time, end_time]);
					this.timeValue1 = this.timeValue1.concat([
						start_auditor_time,
						end_auditor_time
					]);
				}
				opthionList(this, this.prepaidhandleDefaultGet.params);
				this.$set(this, "params", this.prepaidhandleDefaultGet.params);
			}
			if (this.prepaidhandleDefaultGet.goPage) {
				this.params.page = this.prepaidhandleDefaultGet.goPage;
				this.params.per_page = this.prepaidhandleDefaultGet.pageSize;
			}
			this.$set(this, "formInlineParams", {
				params: this.params, //1默认带的参数
				TableData: this.TableData, //列表数据
				API: "getPrepaidList", //接口名
				vm: this //传递的this
			});
			getData(this.formInlineParams);
		}
	};
</script>

<style scoped>
</style>
